  .parallax-right {
    position: absolute;
    right: -10%;
    bottom: -10%;
  }

  .bg-objects .bg-trees-left {
    margin: 0;
    padding: 0;
    bottom: -10%;
    width: 184px;
  }

  .bg-objects .bg-trees-right {
